import classNames from 'classnames'
import React, { useEffect, useState, useContext } from 'react'

import { AppDataContext } from '../stores/AppDataContext'

const ScreenSizeDetector = () => {
  const { isScreenSizeSmall } = useContext(AppDataContext)

  return (
    <div
      className={classNames(
        'position-fixed vw-100 vh-100 z-3 d-flex flex-column align-items-center justify-content-center bg-secondary-emphasis',
        {
          'd-none': !isScreenSizeSmall,
        }
      )}
    >
      <img src="/playground_assets/expand.png" className="w-50" />
      <p className="h2 text-white mt-4 text-center px-4">
        YOUR SCREEN IS TOO SMALL.
      </p>
    </div>
  )
}

export default ScreenSizeDetector
